  width: 40%;
}
.txt {
  color: #65888c;
}
.program {
  color: #4e9a06;
}
.dir {
  color: #3465a4;
}
* {
  margin: 0;
  padding: 0;
}
body {
  background: #002b36;

  h1,
  h2,
  h3 {
    font-family: "Inconsolata", monospace;
  }

  p {
    color: #65888c;
    font-size: 20px;
    font-family: "Montserrat", sans-serif;
  }

  pre {
    width: 100%;
    padding: 0;
    margin-top: 1em;
    overflow: auto;
    overflow-y: hidden;

    code {
      padding: 10px;
      color: #333;
      margin: 5px;
    }
  }

  .vue-command {
    .term-bar {
      position: fixed;
      -webkit-touch-callout: none;
      -webkit-user-select: none;
      -khtml-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
    }

    .term-std {
      margin-top: 52px;
      height: 100%;
    }
  }
}
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #252525;
}

::-webkit-scrollbar-thumb {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb:hover {
  background: #333;
}
